body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "IBM Plex Sans", sans-serif;
  background-color: #0d0e19;
  color: #e8f1fb;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.2;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  position: relative;
  z-index: 1;
}

section {
  transition: opacity 1s ease;
}

a {
  color: #e8f1fb;
  text-decoration: none;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.d-none-desktop {
  display: none !important;
}
@media only screen and (max-width: 767px) {
  .d-none-desktop {
    display: block !important;
  }
}

.d-none-mobile {
  display: block !important;
}
@media only screen and (max-width: 767px) {
  .d-none-mobile {
    display: none !important;
  }
}

.d-none-desktop-flex {
  display: none !important;
}
@media only screen and (max-width: 767px) {
  .d-none-desktop-flex {
    display: flex !important;
  }
}

.d-none-mobile-flex {
  display: flex !important;
}
@media only screen and (max-width: 767px) {
  .d-none-mobile-flex {
    display: none !important;
  }
}

.hide {
  opacity: 0 !important;
}

/* When hystmodal opened */
.hystmodal__opened .header {
