/* Swiper */
.swiper-nav {
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 10;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  pointer-events: none;
}
@media only screen and (max-width: 767px) {
  .swiper-nav {
    display: none;
  }
}
.swiper-nav .swiper-btn-prev,
.swiper-nav .swiper-btn-next {
  position: relative;
  cursor: pointer;
  pointer-events: auto;
}
.swiper-nav .swiper-btn-prev:hover svg,
.swiper-nav .swiper-btn-next:hover svg {
  color: rgba(85, 221, 129, 0.7);
}
.swiper-nav .swiper-btn-prev svg,
.swiper-nav .swiper-btn-next svg {
  color: #55dd81;
  transition: 0.3s ease;
}

.swiper-pagination {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 36px;
}
@media only screen and (max-width: 991px) {
  .swiper-pagination {
    gap: 20px;
  }
}
@media only screen and (max-width: 767px) {
  .swiper-pagination {
    gap: 10px;
  }
}
.swiper-pagination__bullet {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 94px;
  height: 94px;
  background: transparent;
  opacity: 1;
  border-radius: 28px;
  border: 4px solid #55dd81;
  cursor: pointer;
  transition: 0.3s ease;
}
@media only screen and (max-width: 991px) {
  .swiper-pagination__bullet {
    width: 70px;
    height: 70px;
    border-radius: 24px;
  }
}
@media only screen and (max-width: 767px) {
  .swiper-pagination__bullet {
    width: 50px;
    height: 50px;
    border-radius: 18px;
    border: 2px solid #55dd81;
  }
}
.swiper-pagination__bullet:hover {
  background-color: #55dd81;
}
.swiper-pagination__bullet:hover svg {
  color: #ffffff;
}
.swiper-pagination__bullet svg {
  color: #55dd81;
  width: 75%;
  height: 75%;
  transition: 0.3s ease;
}
.swiper-pagination__bullet--active {
  background-color: #55dd81;
}
.swiper-pagination__bullet--active svg {
  color: #ffffff;
}
.swiper-pagination .swiper-pagination-bullet-active {
  width: 94px;
  height: 94px;
}

/* Adv mark article */
.token-mark-block {
  position: relative;
  z-index: 20;
  display: block;
  max-width: 1288px;
  width: 100%;
  padding: 0 20px;
  margin: 30px auto 0;
  box-sizing: border-box;
}

.article-mark:hover + .token-info {
  opacity: 1;
  pointer-events: auto;
}

.token-info {
  position: absolute;
  left: 0;
  bottom: 100%;
  width: 270px;
  border: 2px solid #ffffff;
  background: rgba(85, 221, 129, 0.7);
  backdrop-filter: blur(11px);
  border-radius: 2px;
  padding: 20px 10px;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
}

.token-info-item {
  display: block;
  color: #ffffff;
  font-size: 12px;
  line-height: 1.2;
}
.token-info-item a {
  color: #ffffff;
  text-decoration: underline;
}
.token-info-item a:hover {
  text-decoration: none;
}

/* Blocks */
